import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/store.js'

Vue.config.productionTip = false

/* eslint-disable no-new */
window.htsBus = new Vue();

new Vue({
	el: '#app',
	store,
	router,
	template: '<App/>',
	components: { App }
})







